import { keyboardColorsType } from "../gameWrapper";
import Key from "./key";

type KeyboardProps = {
  onKeyboardInput: (letter: string) => void;
  onDelete: () => void;
  onCheck: () => void;
  keyboardColors: keyboardColorsType;
};

const Keyboard = ({
  onKeyboardInput,
  onDelete,
  onCheck,
  keyboardColors,
}: KeyboardProps) => {
  const firstLine = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
  ];
  const middleLine = ["", "", "", "", "", "", "", "", "", "", ""];
  const lastLine = ["", "", "", "", "", "", "", "", ""];

  const keyStyleCheck = (symbol: string) => {
    return keyboardColors.green.has(symbol)
      ? "G"
      : keyboardColors.yellow.has(symbol)
        ? "Y"
        : keyboardColors.black.has(symbol)
          ? "B"
          : "D";
  };

  return (
    <div className="flex flex-col container w-full max-w-lg pb-2 md:pb-5 px-2 mx-auto">
      <div className="flex">
        {firstLine.map((symbol, index) => {
          return (
            <Key
              key={index}
              letter={symbol}
              handleClick={(letter) => onKeyboardInput(letter)}
              keyStyle={keyStyleCheck(symbol)}
            ></Key>
          );
        })}
      </div>
      <div className="flex">
        {middleLine.map((symbol, index) => (
          <Key
            key={index}
            letter={symbol}
            handleClick={(letter) => onKeyboardInput(letter)}
            keyStyle={keyStyleCheck(symbol)}
          ></Key>
        ))}
      </div>
      <div className="flex">
        <Key letter="<=" handleClick={onDelete} keyStyle="D"></Key>
        {lastLine.map((symbol, index) => (
          <Key
            key={index}
            letter={symbol}
            handleClick={(letter) => onKeyboardInput(letter)}
            keyStyle={keyStyleCheck(symbol)}
          ></Key>
        ))}
        <Key letter="" handleClick={onCheck} keyStyle="D"></Key>
      </div>
    </div>
  );
};

export default Keyboard;
