import { Button, Flex, Form, Input, message, Modal, Space } from "antd";
import { MessageInstance } from "antd/es/message/interface";

type CreateWordByFriendPopupProps = {
  isPopupOpen: boolean | undefined;
  onCancel: () => void;
  messageApi: MessageInstance;
};

const CreateWordByFriendPopup = ({
  isPopupOpen,
  onCancel,
  messageApi,
}: CreateWordByFriendPopupProps) => {
  const [form] = Form.useForm();
  const handleCreateWordByFriend = async () => {
    const value = await form.validateFields();
    const url = `${window.location.origin}/${encodeURIComponent(value.word).replaceAll("%", "-")}`;
    navigator.clipboard
      .writeText(url)
      .then(() => {
        messageApi.open({
          content: "Cc    .",
        });
      })
      .catch((err) => {
        messageApi.open({
          content: "   .",
        });
      });
  };

  return (
    <Modal
      width={360}
      title={
        <Space className="w-full" direction="vertical" align="center">
            
        </Space>
      }
      open={isPopupOpen}
      onCancel={onCancel}
      centered
      styles={{ mask: { backgroundColor: "#111827cc" } }}
      footer={
        <Space className="w-full" direction="vertical" align="center">
          <Button
            color="green"
            variant="solid"
            size="large"
            onClick={handleCreateWordByFriend}
          >
            
          </Button>
        </Space>
      }
    >
      <Flex vertical={true} gap={16}>
        <p className="text-center">
                    5 .
        </p>
        <Form form={form} name="basic">
          <Form.Item
            name="word"
            rules={[
              {
                required: true,
                message: "   5  !",
                max: 5,
                pattern: /^[--]{5}$/,
              },
            ]}
          >
            <Input
              size="large"
              placeholder="   5  !"
            />
          </Form.Item>
        </Form>
      </Flex>
    </Modal>
  );
};

export default CreateWordByFriendPopup;
